}
#kecheng .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#kecheng .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#kecheng .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#kecheng .is-top :hover{
    color: #029348 !important;
}
#kecheng .is-left :hover{
    color: #029348 !important;
}
#kecheng .el-card{
    padding: 40px 0 40px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>




